<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import Loader from '../../components/Loader';
import eventHub from '../../utils/eventhub';
import form from '../../mixins/form';
import { success, fail } from '../../utils/toast';
import classes from '../../utils/validations/classes';
import { updateClassModule, getClassModule } from '../../utils/api/classes';
import AddModuleQuiz from './AddModuleQuiz';
import AdditionalContent from './AdditionalContent';
import AdditionalContentModal from './AdditionalContentModal';
import init from '../../config/tinymce';

export default {
	name: 'ModuleDetail',
	mixins: [form],
	data() {
		return {
			init,
			loading: true,
			itemData: {},
			errors: {},
			quizzes: [],
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ header: 1 }, { header: 2 }],
						[{ list: 'ordered' }, { list: 'bullet' }],
						[{ script: 'sub' }, { script: 'super' }],
						[{ indent: '-1' }, { indent: '+1' }],
						[{ direction: 'rtl' }],
						[{ size: ['small', false, 'large', 'huge'] }],
						[{ header: [1, 2, 3, 4, 5, 6, false] }],
						[{ font: [] }],
						[{ color: [] }, { background: [] }],
						[{ align: [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	components: {
		Loader,
		AddModuleQuiz,
		AdditionalContent,
		AdditionalContentModal,
		quillEditor
	},
	created() {
		this.getClassModule();
		eventHub.$on('module-quiz-created', (payload) => {
			this.quizzes.unshift(payload);
		});
	},
	methods: {
		getClassModule() {
			const { classId, lessonId, moduleId } = this.$route.params;
			getClassModule(classId, lessonId, moduleId)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.quizzes = response.data.data.quiz;
						this.loading = false;
					}
				})
				.catch((error) => {
					this.loading = false;
					return error;
				});
			return true;
		},
		updateClassModule() {
			const { content, title } = this.itemData;
			const result = { content, title };

			this.validate(result, classes.updateModule);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}

			const { classId, lessonId, moduleId } = this.$route.params;

			updateClassModule(classId, lessonId, moduleId, result)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						success({
							title: 'Success! Module has been updated!'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	computed: {
		editor() {
			return this.$refs.myTextEditor.editor;
		}
	}
};
</script>


