<template>

			<div class="modal-content">




				<div id="popup-article" class="popup">
					<div class="popup__block">

						<div class="modal-header modal__header">
							<h3 class="modal-title" id="contentModalTitle">Add Additional Content</h3>

						</div>
						<div class="modal-body modal__body">
							<form
								@submit.prevent="createContent"
								class="modal__form"
								method="post">
								<div class="form-group">
									<label class="modal__label" for="class-title">Title</label>
									<input
										type="text"
										:class="[{'is-invalid': errors.title}]"
										class="form-control"
										id="class-title"
										placeholder="Class Title"
										@keydown="clearErrors($event.target.name)"
										v-model.trim="itemData.title">
									<div class="invalid-feedback">
										{{ errors.title }}
									</div>
								</div>
								<div class="form-group">
									<label class="modal__label" for="class-content">Content</label>
									<!--<wysiwyg v-model="itemData.content" />-->
									<editor :init="init" ref="myTextEditor" v-model="itemData.content"></editor>
									<div class="invalid-feedback">
										{{ errors.content }}
									</div>
								</div>
							</form>
						</div>
						<div class="modal-footer modal__footer">
							<Loader v-if="loading"/>

							<a href="#">
							<button
								@click="clearErrors"
								class="btn btn--md"
								type="button"
								name="button">Cancel</button>
							</a>
							<button
								@click.prevent="createContent"
								class="btn btn--md btn__primary"
								type="submit"
								name="button">
								Save Content</button>
						</div>
						<a href="#" class="popup__close">close</a>
					</div>
				</div>




			</div>

</template>

<script>
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import { createAdditionalContent } from '../../utils/api/classes';
import { fail, success } from '../../utils/toast';
import classes from '../../utils/validations/classes';
import eventHub from '../../utils/eventhub';
import init from '../../config/tinymce';

export default {
	name: 'AdditionalContentModal',
	mixins: [form],
	data() {
		return {
			itemData: {},
			errors: {},
			loading: false,
			init
		};
	},
	methods: {
		createContent() {
			this.validate(this.itemData, classes.content);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			createAdditionalContent(this.$route.params.moduleId, this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.loading = false;
						this.itemData = {};
						eventHub.$emit('content-created', response.data);
						success({
							title: 'Success! Additional content has been added'
						});
					}
					return false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	components: {
		Loader
	},
	computed: {

	}
};
</script>
