<template>
    <div>
        <template v-if="loadingContent">
			<div class="loading">
				<Loader />
			</div>
		</template>
        <template v-else-if="contents && contents.length">
            <div 
                v-for="content in contents"
                :key="content.id"
                class="contacts--list__item content">
                <p class="info__email">
                    {{content.title}}
                </p>		
                <p v-html="content.content"></p>
				<p  class="btn__row">
					<button
						class="btn btn__danger"
						@click="deleteAnAdditionalContent(content)">
						Delete
					</button>
					<router-link
						:to="{ name: 'additional-content-detail',
						params: { 
							id: content.id,
							classId: $route.params.classId,
							lessonId: $route.params.lessonId,
							moduleId: $route.params.moduleId
							}}"
						class="btn btn__primary">
						View
					</router-link>
				</p>
            </div>
        </template>
        <template v-else>
            <p>No additional created yet</p>
        </template>
    </div>
</template>

<script>
import { getAdditionalContent, deleteAnAdditionalContent } from '../../utils/api/classes';
import Loader from '../../components/Loader';
import eventHub from '../../utils/eventhub';
import { success, fail } from '../../utils/toast';

export default {
	name: 'AdditionalContent',
	data() {
		return {
			contents: [],
			loadingContent: true,
			contentLoaded: false
		};
	},
	created() {
		this.getContent();
		eventHub.$on('content-created', (payload) => {
			this.contents.unshift(payload);
		});
	},
	methods: {
		getContent() {
			getAdditionalContent(this.$route.params.moduleId)
				.then((response) => {
					if (response.status === 200) {
						this.contents = response.data;
						this.contentLoaded = false;
						return true;
					}
					this.contentLoaded = false;
					return false;
				})
				.catch((error) => {
					this.contentLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingContent = false;
				});
			return true;
		},
		deleteAnAdditionalContent(item) {
			deleteAnAdditionalContent(this.$route.params.moduleId, item.id)
				.then((response) => {
					if (response.status === 200) {
						success({
							title: 'Success! Module has been deleted!'
						});
						this.contents.splice(this.contents.indexOf(item), 1);
						this.loading = false;
					}
				})
				.catch((error) => {
					fail({
						title: 'Oops! Something went wrong',
						text: error.data
					});
					this.loading = false;
					return error;
				});
			return true;
		}
	},
	components: {
		Loader
	}
};
</script>

<style lang="scss" scoped>
.contacts--list__item {
    &.content {
        grid-template-columns: repeat(2, 2fr) 1fr;
    }
}
.btn__danger {
    background-color: #fff;
    color: #DD4B39;
    border-color: #DD4B39;
	&:hover {
		background: #DD4B39;
		color: #fff;
	}
}
.btn__row {
	display: flex;
	justify-content: space-between;
}
</style>
